











import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class ArrowButton extends Vue {
  /**
   * ボタンが使用できるかどうか
   */
  @Prop() private enabled!: boolean;

  /**
   * ボタンクリック時のリスナーメソッド
   * 'click'イベントを発火する
   */
  protected emitButtonClick(): void {
    this.$emit('click');
  }
}
